body {
    margin: auto;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
.root {
    margin: auto;
}
.page {
    background: black;
    margin: auto;
    position: relative;
    scroll-behavior: smooth;
    font-family: Bellefair;
}
.header {
    margin: auto;
}
.menu__item {
    list-style-type: none;
    opacity: 1;
    transition: opacity .2s ease-in-out .2s;
}

.menu__item:hover {
    opacity: .6;
}
.menu__item-text {
    color: white;
    text-decoration: none;
    font-size: 24px;
}
.menu {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 18px 0 0;
}
.lead__text {
    color: white;
    font-size: 36px;
    text-align: center;
    margin-bottom: 0;
}
.lead {
    padding: 25px 0 25px;
    margin: auto;
}
.map__container {
    margin: auto;
    position: relative;
}
.map__location {
    border-radius: 50%;
    position: absolute;
    width: 100px;
    height: 50px;
    cursor: pointer;
    background-color: rgba(80, 5, 5, 0.2);
    transition: background-color .05s ease-in-out .05s;
}

@media screen and (max-width: 1100px) {
    .map__location {
        width: 60px;
        height: 40px;
    }
}

@media screen and (max-width: 800px) {
    .map__location {
        width: 40px;
        height: 28px;
    }
}

@media screen and (max-width: 600px) {
    .map__location {
        width: 30px;
        height: 18px;
    }
}

@media screen and (max-width: 425px) {
    .map__location {
        width: 25px;
        height: 12px;
    }
}

.map__location:hover {
    background-color: rgba(165, 19, 14, 0.4)
}
.map__location_perdido-street-station {
    top: 35%;
    left: 42%;
}

.map__location_brock-marsh {
    top: 42%;
    left: 54%;
}

.map__location_glasshouse {
    top: 50%;
    left: 43.5%;
}

.map__location_bonetown {
    top: 43%;
    right: 30%;
}

.map__location_kinken {
    top: 52%;
    left: 35%;
}

.map__location_griss-twist {
    top: 50%;
    right: 39%;
}

.map__location_spatters {
    bottom: 33%;
    left: 15%;
}

.map__location_salacus-fields {
    bottom: 36%;
    left: 37%;
}

.map__location_new-crobuzon-university {
    top: 31%;
    right: 32.5%;
}

.map__location_dog-fenn {
    bottom: 43%;
    right: 14%;
}

.map__location_strack-island {
    top: 48%;
    right: 35%;
}

.map__location_canker-wedge {
    top: 16.5%;
    left: 39%;
}

.map__location_flag-hill {
    top: 7.5%;
    right: 39%;
}

.map__location_nigh-sump {
    top: 26%;
    right: 17%;
}

.map__location_spit-hearth {
    top: 37.5%;
    left: 46%;
}
.map {
    width: 100%;
}
.races {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    grid-template-rows: repeat(auto-fit, minmax(350px, 1fr));
    grid-gap: 40px;
    padding: 50px 0 50px;
    max-width: 80%;
    margin: auto;
}
.race-card__image {
    object-fit: contain;
    max-width: 100%;
    max-height: 282px;
    border-radius: 50%;
    z-index: 3;
    margin: 15px;
}
.race-card__title {
    text-align: center;
    color: white;
    font-size: 30px;
    margin: 0 0 15px;
    z-index: 3;
}
.race-card__description {
    color: white;
    z-index: 3;
}
.race-card {
    display: flex;
    flex-direction: column;
    background-image: url('https://i.pinimg.com/originals/6a/72/de/6a72deda9a726ccb667916f3e1be5fe3.jpg');
    background-size: cover;
    position: relative;
    border-radius: 10%;
    cursor: pointer;
}

.race-card::before {
    content:"";
    position: absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    display: block;
    background-color: rgba(0,0,0,.4);
    z-index: 2;
}
.section-title {
    color: white;
    text-align: center;
    font-size: 56px;
    text-decoration: underline;
}
.books__cards {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    max-width: 80%;
    margin: auto;
    list-style-type: none;
    padding: 0;
}
.books__image {
    width: 240px;
    height: 350px;
    cursor: pointer;
    transition: -webkit-transform .3s ease-in-out .3s;
    transition: transform .3s ease-in-out .3s;
    transition: transform .3s ease-in-out .3s, -webkit-transform .3s ease-in-out .3s;
}

.books__image:hover {
    transform: scale(1.25);
    -moz-transform: scale(1.25);
   -ms-transform: scale(1.25);
   -webkit-transform: scale(1.25);
   -o-transform: scale(1.25);
}
.books__book {
    list-style-type: none;
}
.books {
    background-color: rgb(63, 11, 11);
    padding: 10px 0 50px;
}
.footer__text {
    text-align: left;
    color: white;
    font-size: 20px;
    margin-top: 70px;
}
.footer {
    height: 75px;
}
.popup__container {
    position: relative;
    max-width: 75vw;
    padding: 100px;
    border-radius: 50%;
    display: flex;
}
.popup__container_place {
    flex-direction: row;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.4);
}
.popup__container_race {
    flex-direction: column;
    background-color: rgba(80, 6, 6, 0.6);
}
.popup__exit {
    height: 40px;
    width: 40px;
    position: absolute;
    top: 40px;
    right: 30px;
    cursor: pointer;
    border: none;
    transition: opacity .5s ease-in-out .5s;
}

.popup__exit:hover {
    opacity: 0.6;
}
.popup__image {
    max-width: 50%;
    max-height: 350px;
    object-fit: contain;
}
.popup__info-container {
    display: flex;
    flex-direction: column;
    margin-left: 20px;
    max-width: 50%;
}
.popup__title {
    color: white;
    text-align: center;
    font-size: 44px;
    margin: 15px 0 0;
}
.popup__text {
    color: white;
    max-width: 40vw;
    font-size: 24px;
}
.popup__race-image {
    max-height: 50vh;
    width: 60%;
    max-width: 450px;
    margin: auto;
}
.popup {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    justify-content: center;
    align-items: center;
    background-color:rgba(0, 0, 0, 0.5);
    z-index: 3;
    opacity: 0;
    visibility: hidden;
    transition: visibility 1s, opacity 1s ease;
}
.popup_visible {
    opacity: 1;
    visibility: visible;
}







